@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;800&display=swap);
.App {
  text-align: center;
  background: linear-gradient(to top,#9986EB ,#65B9EB);
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #174ab1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  font-family: 'Poppins', sans-serif !important;
  font-weight: 600;
}
/****************************************** NAVBAR ********************************************/

nav {
  background: #65B9EB;
  /* background: linear-gradient( #9986EB,#65B9EB); */
  opacity: 0.8;
  z-index: 2;
}

.logo {
  width: 4.5rem;
  margin-left: 8.55rem;
  margin-top:  1rem;
}

.navbar-collapse {
  margin-right: 7.2rem;
  text-align: right;
}
.navbar-toggler {
  margin-right: 1.5rem;
  color: #ffffff;
}

/*----- MENU LINKS --------*/
nav a.nav-link {
  font-size: 20px;
  letter-spacing: 0.03rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 1.2rem;
  margin-left: 1.2rem;
  color: #ffffff !important;
  text-align: right;
}

.navbar-brand {
  max-width: 20rem;
  padding: 0 !important;
  height: 5rem !important;
}

.navbar-nav > li > a.active,
.navbar-nav > li > a.active:focus {
  border-bottom: 0.1875rem solid #9986EB;
}

.navbar-nav > li > a:hover {
  border-bottom: 0.1875rem solid #9986EB;
}

